import { getUuid, isWheel, offset, position } from '../base/util';

const win = window;
const doc = document;
const { forEach, map } = Array.prototype;

class ScrollSpy {
  static defaultConfig = {
    offset: 0,
    method: 'auto',
    menu: '', // 
    menuCls: null, // 
    menuClsPrefix: 'menu', // 
    container: null, // 
    immedLoad: true, //  false 
    prefix: 'menu-', // 
    anchor: true, // 
    animation: true, // 
    extend: false, // 
  };

  constructor(element, config = {}) {
    this.element = element;
    this.scrollElement = element.tagName === 'BODY' ? win : element;
    this.config = this.getConfig(config);
    const { menu, menuClsPrefix, extend } = this.config;
    //   
    this.menuSelector = `${menu} .${menuClsPrefix}-title`;
    this.offsets = [];
    this.targets = [];
    this.activeTarget = null;
    this.scrollHeight = 0;

    // 
    if (!extend) {
      const extendStyle = doc.createElement('style');
      extendStyle.innerHTML = `
          .menu-catalogue .menu-catalogue {
            display: none;
          }
          .active + .menu-catalogue {
            display: block;
          }`;
      doc.getElementsByTagName('head')[0].append(extendStyle);
    }

    // 
    this.scrollElement.addEventListener(
      isWheel ? 'mousewheel' : 'DOMMouseScroll',
      this.scrollEvent,
      false
    );
    const { immedLoad, initMenus, anchor } = this.config;
    // 
    if (initMenus) {
      this.generateMenus();
    }

    // 
    this.menuPanel = `${menu} .${menuClsPrefix}.${menuClsPrefix}-catalogue`;
    this.$menuPanel = doc.querySelector(this.menuPanel);

    // 
    if (anchor === false) {
      // fixme  
      const menuElements = doc.querySelectorAll(this.menuSelector);
      forEach.call(menuElements, el => {
        el.addEventListener(
          'click',
          e => {
            e.preventDefault();
            e.stopPropagation();
            const target = e.currentTarget;
            const { offsetMethod, container } = this.config;
            const offsetXY = this.config.offset;

            const targetEl = doc.querySelector(
              `${container} [data-target="${target.dataset}"]`
            ).parentNode;
            const top =
              offsetMethod === 'offset'
                ? offset(targetEl).top
                : position(targetEl).top;
            const offsetBase =
              offsetMethod === 'position' ? this.getScrollTop() : 0;

            // fixme
            this.scrollElement.scrollTo(0, top + offsetBase - offsetXY);
            target.blur();
            this.process();
          },
          false
        );
      });
    }

    // 
    const { pluginConfig } = this.config;
    let { plugins } = this.config;
    if (plugins) {
      if (!Array.isArray(plugins)) {
        plugins = [plugins];
      }
      this.plugins = [];
      const { container } = this.config;
      plugins.forEach(plugin => {
        // 
        const instance = new plugin(this, pluginConfig);
        instance.mount();
        this.plugins.push(instance);
      });
    }

    /*
     * 
     * doc.querySelector(menu).style.display = 'block';
     */
    if (immedLoad) {
      this.refresh();
      this.process();
    }
  }

  getConfig(config) {
    const _config = { ...ScrollSpy.defaultConfig, ...config };

    const { menu } = _config;
    let { container } = _config;

    if (typeof menu !== 'string') {
      // dom 
      let { id } = menu;
      if (!id) {
        id = getUuid('scrollspy');
        menu.id = id;
      }
      _config.menu = `#${id}`;
    }

    if (!container) {
      container = doc.body; //  body
    }
    if (typeof container !== 'string') {
      let { id } = container;
      if (!id) {
        id = getUuid('scrollspy');
        container.id = id;
      }
      _config.container = `#${id}`;
    }

    const autoMethod =
      this.scrollElement !== this.scrollElement.window ? 'position' : 'offset';

    const offsetMethod =
      _config.method === 'auto' ? autoMethod : _config.method;

    _config.offsetMethod = offsetMethod;

    return _config;
  }

  /**
   * 
   */
  generateMenus() {
    const menus = this.element.querySelectorAll('h1,h2,h3,h4,h5,h6');
    // 
    const entries = {
      level: 0,
      children: [],
      depth: 0,
      parent: null,
      text: null,
      root: true,
    };

    //  level
    let prevLevel = 0;
    // 
    let lastNode = null;

    forEach.call(menus, (el, index) => {
      let entry = null;
      const level = 7 - parseInt(el.tagName[1], 10);
      const text = el.innerHTML;

      if (index === 0) {
        // 
        entry = {
          level,
          children: [],
          depth: 1,
          parent: entries,
          text,
          el,
        };
        entries.children.push(entry);
      } else {
        if (level === prevLevel) {
          // 
          entry = {
            level,
            children: [],
            depth: lastNode.depth,
            parent: lastNode.parent,
            text,
            el,
          };
          lastNode.parent.children.push(entry);
        } else if (level < prevLevel) {
          // 
          entry = {
            level,
            children: [],
            depth: lastNode.depth + 1,
            parent: lastNode,
            text,
            el,
          };
          lastNode.children.push(entry);
        } else {
          //  
          let ancestor = lastNode.parent;
          while (ancestor.level <= level && !ancestor.root) {
            ancestor = ancestor.parent;
          }
          entry = {
            level,
            children: [],
            depth: ancestor.depth + 1,
            parent: ancestor,
            text,
            el,
          };
          ancestor.children.push(entry);
        }
      }
      lastNode = entry;
      prevLevel = level;
    });

    const html = this.generateMenusHtml(entries.children, this.config.prefix);
    doc.querySelector(this.config.menu).innerHTML = html;
  }

  /**
   *  nodes   html
   * @param nodes
   * @param prefix
   * @return {string}
   */
  generateMenusHtml(nodes, prefix) {
    const { menuClsPrefix, menuCls } = this.config;
    let html = '';
    if (nodes && nodes.length > 0) {
      html = `<ul class="${menuClsPrefix} ${menuClsPrefix}-catalogue${
        menuCls ? ` ${menuCls}` : ''
      }">`;
      for (let i = 0, len = nodes.length; i < len; i++) {
        const node = nodes[i];
        // 
        const newChild = document.createElement('a');
        newChild.id = prefix + (i + 1);
        newChild.className = `${menuClsPrefix}-anchor`;
        if (this.config.anchor) {
          newChild.href = `#${prefix + (i + 1)}`;
        } else {
          newChild.dataset.menu = `${prefix + (i + 1)}`;
        }

        newChild.setAttribute('aria-hidden', true);
        node.el.insertBefore(newChild, node.el.firstChild);

        html += '<li>';
        if (this.config.anchor) {
          html += `<a class="${menuClsPrefix}-title" href="#${prefix}${i +
            1}">${node.text}</a>`;
        } else {
          //  data-target  js 
          html += `<a class="${menuClsPrefix}-title" data-target="${prefix}${i +
            1}" href="#">${node.text}</a>`;
        }
        if (node.children && node.children.length > 0) {
          html += this.generateMenusHtml(node.children, `${prefix}${i + 1}-`);
        }
        html += '</li>';
      }
      html += '</ul>';
    }
    return html;
  }

  /**
   * 
   */
  refresh() {
    const { offsetMethod } = this.config;
    const offsetBase = offsetMethod === 'position' ? this.getScrollTop() : 0;

    this.offsets = [];
    this.targets = [];
    this.scrollHeight = this.getScrollHeight();

    // offsetstargets
    const menuElements = doc.querySelectorAll(this.menuSelector);

    map
      .call(menuElements, element => {
        // 
        let target = null;
        const { anchor, container } = this.config;
        const selector = anchor
          ? element.getAttribute('href')
          : element.dataset.menu;
        const targetSelector = anchor
          ? element.getAttribute('href')
          : `${container} [data-target="${element.dataset.menu}"]`;

        if (targetSelector) {
          target = doc.querySelector(targetSelector).parentNode;
          const targetBCR = target.getBoundingClientRect();
          if (targetBCR.width || targetBCR.height) {
            return [
              (offsetMethod === 'offset'
                ? offset(target).top
                : position(target).top) + offsetBase,
              selector,
            ];
          }
        }
        return null;
      })
      .sort((a, b) => {
        if (!a || !b) {
          return 0;
        }
        return a[0] - b[0];
      })
      .forEach(item => {
        if (item) {
          this.offsets.push(item[0]);
          this.targets.push(item[1]);
        }
      });
  }

  // 
  scrollEvent = e => {
    this.process();
  };

  /**
   * 
   * @returns {boolean|*}
   */
  process() {
    //  1 
    const scrollTop = this.getScrollTop() + this.config.offset + 1;
    const scrollHeight = this.getScrollHeight();
    const maxScroll =
      this.config.offset + scrollHeight - this.getOffsetHeight();

    // 
    if (this.scrollHeight !== scrollHeight) {
      this.refresh();
    }

    // 
    if (scrollTop >= maxScroll) {
      // 
      const target = this.targets[this.targets.length - 1];

      if (this.activeTarget !== target) {
        this.activate(target);
      }
      return;
    }

    // 
    if (
      this.activeTarget &&
      scrollTop < this.offsets[0] &&
      this.offsets[0] > 0
    ) {
      this.activeTarget = null;
      this.clearActiveCls();
      return;
    }

    for (let i = this.offsets.length; i--; ) {
      const isActiveTarget =
        this.activeTarget !== this.targets[i] &&
        scrollTop >= this.offsets[i] &&
        (this.offsets[i + 1] === undefined || scrollTop < this.offsets[i + 1]);

      if (isActiveTarget) {
        this.activate(this.targets[i]);
      }
    }
  }

  /**
   * 
   * @param target
   */
  activate(target) {
    this.activeTarget = target;

    this.clearActiveCls();

    const { anchor } = this.config;
    let queries = this.menuSelector.split(',');
    const lastSelector = []; // 
    // 
    const parentEls = target.split('-');
    queries = queries.map(query => {
      const selector = [];
      let catalog = '';
      parentEls.forEach((item, index) => {
        if (index === 0) {
          catalog += item;
        } else {
          catalog += `-${item}`;
          selector.push(
            anchor
              ? `${query}[href="${catalog}"]`
              : `${query}[data-menu="${catalog}"]`
          );
        }
      });
      lastSelector.push(selector[selector.length - 1]);
      return selector.join(',');
    });

    const $link = doc.querySelectorAll(queries.join(','));

    for (let i = 0, len = $link.length; i < len; i++) {
      $link[i].classList.add('active');
    }

    this.plugins.forEach(plugin => {
      if (typeof plugin.scrollMenu === 'function') {
        plugin.scrollMenu(lastSelector);
      }
    });

    // todo 

    /*
     * $(this._scrollElement).trigger(Event.ACTIVATE, {
     * relatedTarget: target
     *})
     */
  }

  clearActiveCls() {
    // 
    const activeEls = doc.querySelectorAll(`${this.menuSelector}.active`);
    if (activeEls) {
      for (let i = 0, len = activeEls.length; i < len; i++) {
        activeEls[i].classList.remove('active');
      }
    }
  }

  // 
  unmount() {
    // $.removeData(this._element, DATA_KEY)
    this.scrollElement.removeEventListener(
      isWheel ? 'mousewheel' : 'DOMMouseScroll',
      this.scrollEvent,
      false
    );

    this.element = null;
    this.scrollElement = null;
    this.config = null;
    this.menu = null;
    this.menuSelector = null;
    this.container = null;
    this.offsets = null;
    this.targets = null;
    this.activeTarget = null;
    this.scrollHeight = null;

    // 
    if (this.plugins) {
      this.plugins.forEach(plugin => {
        plugin.unmount();
      });
      this.plugins = null;
    }
  }

  getScrollTop() {
    return this.scrollElement === win
      ? this.scrollElement.pageYOffset
      : this.scrollElement.scrollTop;
  }

  getScrollHeight() {
    return (
      this.scrollElement.scrollHeight ||
      Math.max(doc.body.scrollHeight, doc.documentElement.scrollHeight)
    );
  }

  getOffsetHeight() {
    return this.scrollElement === win
      ? win.innerHeight
      : this.scrollElement.getBoundingClientRect().height;
  }
}

export default ScrollSpy;
