const win = window;
const doc = document;

/**
 *  css css 
 * @private {boolean|undefined}
 */
let supportsCssVariables_;

/**
 *  passive
 * https://www.cnblogs.com/ziyunfei/p/5545439.html
 * https://github.com/WICG/EventListenerOptions
 * @private {boolean|undefined}
 */
let supportsPassive_;

/**
 *  Edge 15  css  bug
 * @return {boolean}
 */
function detectEdgePseudoVarBug() {
  /*
   * Detect versions of Edge with buggy var() support
   * See: https://developer.microsoft.com/en-us/microsoft-edge/platform/issues/11495448/
   */
  const node = doc.createElement('div');
  node.className = 'ripple-test-edge-var-bug';
  doc.body.appendChild(node);

  /*
   * The bug exists if ::before style ends up propagating to the parent element.
   * Additionally, getComputedStyle returns null in iframes with display: "none" in Firefox,
   * but Firefox is known to support CSS custom properties correctly.
   * See: https://bugzilla.mozilla.org/show_bug.cgi?id=548397
   */
  const computedStyle = win.getComputedStyle(node);
  const hasPseudoVarBug =
    computedStyle !== null && computedStyle.borderTopStyle === 'solid';
  node.remove();
  return hasPseudoVarBug;
}

/**
 *  css 
 * https://caniuse.com/#feat=css-variables
 * 
 * Edge 15+ (15 bug)Firefox 31+ Chrome 49+ Safari 9.1+Opera 36+
 * IOS Safari 9.3+ (Android 5+: Chromium 62+ Opera 37+ Chrome 62+ FireFox 57+ )
 * @param {boolean=} forceRefresh
 * @return {boolean|undefined}
 */

function supportsCssVariables(forceRefresh = false) {
  if (typeof supportsCssVariables_ === 'boolean' && !forceRefresh) {
    return supportsCssVariables_;
  }

  const supportsFunctionPresent =
    win.CSS && typeof win.CSS.supports === 'function';
  if (!supportsFunctionPresent) {
    return;
  }

  const explicitlySupportsCssVars = win.CSS.supports('--css-vars', 'yes');
  /*
   * See: https://bugs.webkit.org/show_bug.cgi?id=154669
   * See: README section on Safari
   */
  const weAreFeatureDetectingSafari10plus =
    win.CSS.supports('(--css-vars: yes)') &&
    win.CSS.supports('color', '#00000000');

  if (explicitlySupportsCssVars || weAreFeatureDetectingSafari10plus) {
    supportsCssVariables_ = !detectEdgePseudoVarBug();
  } else {
    supportsCssVariables_ = false;
  }
  return supportsCssVariables_;
}

/**
 *  passive 
 * https://www.cnblogs.com/ziyunfei/p/5545439.html
 * https://github.com/WICG/EventListenerOptions
 * @param {!Window=} globalObj
 * @param {boolean=} forceRefresh
 * @return {boolean|{passive: boolean}}
 */
function applyPassive(globalObj = window, forceRefresh = false) {
  if (supportsPassive_ === undefined || forceRefresh) {
    let isSupported = false;
    try {
      /* eslint-disable getter-return */
      globalObj.document.addEventListener('test', null, {
        get passive() {
          isSupported = true;
        },
      });
    } catch (e) {
      /*  */
    }

    supportsPassive_ = isSupported;
  }

  return supportsPassive_ ? { passive: true } : false;
}

/**
 *  matches ( webkitMatchesSelector, msMatchesSelector)  :active 
 * @param {!Object} HTMLElementPrototype
 * @return {!Array<string>}
 */
function getMatchesProperty(HTMLElementPrototype) {
  return ['webkitMatchesSelector', 'msMatchesSelector', 'matches']
    .filter(p => p in HTMLElementPrototype)
    .pop();
}

/**
 * 
 * @param {!Event} ev
 * @param {!{x: number, y: number}} pageOffset
 * @param {!ClientRect} clientRect
 * @return {!{x: number, y: number}}
 */
function getNormalizedEventCoords(ev, pageOffset, clientRect) {
  const { x, y } = pageOffset;
  const documentX = x + clientRect.left;
  const documentY = y + clientRect.top;

  let normalizedX;
  let normalizedY;
  // Determine touch point relative to the ripple container.
  if (ev.type === 'touchstart') {
    normalizedX = ev.changedTouches[0].pageX - documentX;
    normalizedY = ev.changedTouches[0].pageY - documentY;
  } else {
    normalizedX = ev.pageX - documentX;
    normalizedY = ev.pageY - documentY;
  }

  return { x: normalizedX, y: normalizedY };
}

export {
  supportsCssVariables,
  applyPassive,
  getMatchesProperty,
  getNormalizedEventCoords,
};
