import util from '../../base/util';

const { isWheel } = util;

const win = window;
const doc = document;
const resizeEvt = 'orientationchange' in win ? 'orientationchange' : 'resize';

const reg = /translateY\(([-\w]+)\)/;

class CatalogueSpy {
  static defaultConfig = {
    menuHeight: null, // 
    step: 30, // 
  };

  constructor(scrollSpy, config = {}) {
    this.scrollSpy = scrollSpy;
    this.config = { ...CatalogueSpy.defaultConfig, ...config };

    let { menuHeight } = this.config;
    if (!menuHeight) {
      menuHeight = document.documentElement.clientHeight - 64;
      this.config.menuHeight = menuHeight;
    }

    const { $menuPanel } = scrollSpy;
    $menuPanel.style.transform = 'translateY(0)';
    $menuPanel.style.maxHeight = `${menuHeight}px`;
    this.$menuPanel = $menuPanel;

    win.addEventListener(resizeEvt, this.adjustMenuHeight, false);
  }

  // 
  adjustMenuHeight = () => {
    const menuHeight = doc.documentElement.clientHeight - 64;
    this.$menuPanel.style.maxHeight = `${menuHeight}px`;
  };

  mount() {
    /*
     * fixme  scrollSpy  web-guide 
     * http://www.zhangxinxu.com/wordpress/2013/04/js-mousewheel-dommousescroll-event/
     */
    this.$menuPanel.addEventListener(
      isWheel ? 'mousewheel' : 'DOMMouseScroll',
      this.handleScroll(true),
      false
    );
  }

  handleScroll = prevent =>
    /* eslint-disable complexity */
    e => {
      const { scrollHeight, clientHeight } = this.$menuPanel;
      const { step } = this.config;
      this.maxOffset = scrollHeight - clientHeight; // 

      // 
      let upDown;
      const { detail, wheelDelta } = e;
      if (detail) {
        if (detail < 0) {
          // up
          upDown = 'up';
        } else if (detail > 0) {
          // down
          upDown = 'down';
        }
      } else if (wheelDelta) {
        //
        if (wheelDelta > 0) {
          // up
          upDown = 'up';
        }
        if (wheelDelta < 0) {
          // down
          upDown = 'down';
        }
      }

      const { transform } = this.$menuPanel.style;

      let y = reg.exec(transform);
      y = y ? parseFloat(y[1], 10) : 0;

      if (
        prevent &&
        this.maxOffset > 0 &&
        ((upDown === 'down' && Math.abs(y) < this.maxOffset) ||
          (upDown === 'up' && y > 0))
      ) {
        e.preventDefault();
        e.stopPropagation();
      }

      if (
        (upDown === 'up' && y === 0) ||
        (upDown === 'down' && Math.abs(y) === this.maxOffset)
      ) {
        return;
      }

      if (upDown === 'up' && y < 0) {
        this.$menuPanel.style.transform = `translateY(${Math.min(
          y + step,
          0
        )}px)`;
      } else if (upDown === 'down' && Math.abs(y) < this.maxOffset) {
        this.$menuPanel.style.transform = `translateY(${Math.max(
          y - step,
          -this.maxOffset
        )}px)`;
      }
    };

  // 
  scrollMenu = lastSelector => {
    const { step } = this.config;
    const { $menuPanel } = this;
    const { scrollHeight, clientHeight } = $menuPanel;
    let maxOffset = scrollHeight - clientHeight; // 
    if (maxOffset === 0) {
      return;
    }
    maxOffset += 1; // 1
    /*
     * 
     * Fixme  $menuPanel.parentElement padding  border 
     */
    const menuRect = $menuPanel.parentElement.getBoundingClientRect();
    const currentTarget = doc.querySelectorAll(lastSelector.join(','));
    if (currentTarget) {
      Array.prototype.forEach.call(currentTarget, el => {
        let rect = el.getBoundingClientRect();
        while (
          rect.top > 0 &&
          rect.bottom > 0 &&
          (rect.top < menuRect.top || rect.bottom > menuRect.bottom)
        ) {
          // 
          const { transform } = $menuPanel.style;
          let y = reg.exec(transform);
          y = y ? parseFloat(y[1], 10) : 0;
          if (rect.top < menuRect.top) {
            $menuPanel.style.transform = `translateY(${Math.min(
              y + step,
              0
            )}px)`;
          } else {
            $menuPanel.style.transform = `translateY(${Math.max(
              y - step,
              -maxOffset
            )}px)`;
          }
          rect = el.getBoundingClientRect();
        }
      });
    }
  };

  unmount() {
    console.info('');
    win.removeEventListener(resizeEvt, this.adjustMenuHeight, false);
  }
}

export default CatalogueSpy;
